import Vue from 'vue'
import VueRouter from "vue-router";
import Project from "@/components/Projects/Project";


Vue.use(VueRouter);

const routes = [
    {
        path:'/',
        redirect:'home',
        params:{
            status : 'home'
        }
    },
    {
        path:'/:status',
        beforeEnter: (to,from,next) => {
            next();
        }
    },
    {
        path:'/:status/:id',
        components:{
            page: Project
        },
        params:{
            status : 'project'
        },
        beforeEnter: (to,from,next) => {
            next();
        }
    }
];

const router = new VueRouter({
    routes,
    mode:'history',
    linkActiveClass:'active',
});
export default router

