<template>
    <div class="introduction" id="introduction">
        <h1 v-html="content"></h1>
    </div>
</template>

<script>
    import $ from "jquery";
    import {gsap,Power2} from 'gsap';
    export default {
        name: "Introduction",
        data(){
            return{
                home: '我是一名设计师，这里是我的数字媒体实验室',
                works:' 产品/交互/媒体/品牌<br>设计多面手',
                contact:'我在<a href="https://www.racefitlab.com" class="interactive-s">RaceFit</a>担任设计组长，并且联合创立了<a href="#" class="interactive-s">随异设计</a>，你可以通过微信与我联系',
                content:'',
            }
        },
        mounted(){
            this.pickContent(this.status);
        },
        watch:{
            status:function () {
                gsap.to($('#introduction'),{
                    duration:0.5,
                    y:-32,
                    opacity:0,
                    ease:Power2.easeOut,
                });
                setTimeout( ()=> {
                    this.pickContent(this.status);
                    window.scrollTo(0, 0);
                },500);
                gsap.to($('#introduction'),{
                    delay:0.5,
                    duration:0.5,
                    y:32,
                    opacity:1,
                    ease:Power2.easeOut,
                });
            }
        },
        methods:{
            pickContent(s){
                switch(s){
                    case 'home':
                        this.content =  this.home;
                        gsap.to($('#introduction'),{
                            duration:0.5,
                            height:100+'vh',
                            opacity:1,
                            ease:Power2.easeOut,
                        });
                        break;
                    case 'works':
                        this.content = this.works;
                        gsap.to($('#introduction'),{
                            duration:0.5,
                            height:100+'vh',
                            opacity:1,
                            ease:Power2.easeOut,
                        });
                        break;
                    case 'contact':
                        this.content = this.contact;
                        gsap.to($('#introduction'),{
                            duration:0.5,
                            height:100+'vh',
                            opacity:1,
                            ease:Power2.easeOut,
                        });
                        break;
                    default:
                        this.content = ''
                        gsap.to($('#introduction'),{
                            duration:0.5,
                            height:0,
                            opacity:0,
                            ease:Power2.easeOut,
                        });
                }
            }
        },
        computed:{
            status:function () {
                return this.$route.params.status;
            }
        }
    }
</script>

<style scoped>
    .introduction {
        height: 100vh;
        position: relative;
        display: flex;
        align-items: center;
        box-sizing: border-box;
        font-size: 0;
    }
    .introduction h1{
        width: 100%;
        word-break: break-word;
        text-align: center;
        color: var(--gray7);
    }
</style>