<template>
    <div id="cursor-mask">
<!--        <div id="cursor">-->
<!--            <img src="../assets/eye-white.png" height="200" width="200"/>-->
<!--        </div>-->
    </div>
</template>

<script>
    import $ from 'jquery';
    import P5 from 'p5';
    require('../assets/eye-white.png');

    export default {
        name: "MyCursor",
        mounted(){
            // eslint-disable-next-line no-prototype-builtins
            const sketch = s =>{
                let largeCursorSize = 16*2;
                let smallCursorSize = 16;
                let tSize = 32;

                let centerX;
                let centerY;
                let radius = 16;
                let rotAngle = -90;
                let accelX = 0.0;
                let accelY = 0.0;
                let deltaX = 0.0;
                let deltaY = 0.0;
                let springing = 0.1;
                let damping = 0.5;

                //corner nodes
                let nodes = 100;

                //zero fill arrays
                let nodeStartX = [];
                let nodeStartY = [];
                let nodeX = [];
                let nodeY = [];
                let angle = [];
                let frequency = [];
                // soft-body dynamics
                let organicConstant = 1.0;
                for (let i = 0; i < nodes; i++) {
                    //initialize arrays to 0
                    nodeStartX[i] = 0;
                    nodeStartY[i] = 0;
                    nodeX[i] = 0;
                    nodeY[i] = 0;
                    angle[i] = 0;
                }
                // initialize frequencies for corner nodes
                for (let i = 0; i < nodes; i++) {
                    frequency[i] = s.random(5, 20);
                }

                s.setup=()=> {
                    s.createCanvas(s.windowWidth,s.windowHeight);
                    s.rectMode(s.CENTER);
                    s.noStroke();
                    centerX = s.mouseX;
                    centerY = s.mouseY;
                };
                s.draw=()=> {
                    s.clear();

                    if(this.isHover){
                        centerX += (this.hoveringObject.getBoundingClientRect().x + this.hoveringObject.offsetWidth / 2 - centerX) * damping;
                        centerY += (this.hoveringObject.getBoundingClientRect().y + this.hoveringObject.offsetHeight / 2 - centerY) * damping;
                        tSize = largeCursorSize;
                        s.fill(255,255,255,128);
                        if ( this.isLargeTarget){
                            deltaX = s.map(s.mouseX - centerX,-300,300,-20,20);
                            deltaY = s.map(s.mouseY - centerY,-300,300,-30,30);
                        }else {
                            deltaX = s.mouseX - centerX;
                            deltaY = s.mouseY - centerY;
                        }

                    }else {
                        tSize = smallCursorSize;
                        s.fill(255);
                        deltaX = s.mouseX - centerX;
                        deltaY = s.mouseY - centerY;
                    }
                    radius += (tSize - radius) * damping;

                    for (let i = 0; i < nodes; i++) {
                        nodeStartX[i] = centerX + s.cos(s.radians(rotAngle)) * radius;
                        nodeStartY[i] = centerY + s.sin(s.radians(rotAngle)) * radius;
                        rotAngle += 360.0 / nodes;
                    }
                    // draw polygon
                    s.curveTightness(organicConstant);

                    s.beginShape();
                    for (let i = 0; i < nodes; i++) {
                        s.curveVertex(nodeX[i], nodeY[i]);
                    }
                    for (let i = 0; i < nodes - 1; i++) {
                        s.curveVertex(nodeX[i], nodeY[i]);
                    }
                    s.endShape(s.CLOSE);



                    // create springing effect
                    deltaX *= springing;
                    deltaY *= springing;
                    accelX += deltaX;
                    accelY += deltaY;

                    // move predator's center
                    centerX += accelX;
                    centerY += accelY;

                    // slow down springing
                    accelX *= damping;
                    accelY *= damping;

                    // change curve tightness
                    organicConstant = 1 - ((s.abs(accelX) + s.abs(accelY)) * 0.1);

                    //move nodes
                    for (let i = 0; i < nodes; i++) {
                        nodeX[i] = nodeStartX[i] + s.sin(s.radians(angle[i])) * (accelX * 2);
                        nodeY[i] = nodeStartY[i] + s.sin(s.radians(angle[i])) * (accelY * 2);
                        angle[i] += frequency[i];
                    }
                };
                s.windowResized=()=>{
                    s.resizeCanvas(s.windowWidth, s.windowHeight);
                }
            };
            new P5(sketch, 'cursor-mask');
            this.blindListener();
        },
        data(){
            return{
                isHover : false,
                isLargeTarget : false,
                hoveringObject: null,
            }
        },
        methods:{
            blindListener:function () {
                $(document).on('mouseover','.interactive-l',(e)=>{
                    this.hoveringObject = e.target;
                    this.isHover = true;
                    this.isLargeTarget = true;
                }).on('mouseout','.interactive-l',()=>{
                    this.hoveringObject = null;
                    this.isHover = false;
                    this.isLargeTarget = false;
                }).on('click','.interactive-l',()=>{
                    this.isHover = false;
                    this.isLargeTarget = false;
                });
                $(document).on('mouseover','.interactive-s',(e)=> {
                    this.hoveringObject = e.target;
                    this.isHover = true;
                    this.isLargeTarget = false;
                }).on('mouseout','.interactive-s',()=>{
                    this.hoveringObject = null;
                    this.isHover = false;
                    this.isLargeTarget = false;
                }).on('click','.interactive-s',()=>{
                    this.isHover = false;
                    this.isLargeTarget = false;
                });

            },
        }
    }
</script>

<style scoped>
    #cursor-mask {
        width: 100vw;
        height: 100vh;
        position: fixed;
        left: 0;
        top: 0;
        pointer-events: none;
        z-index: 99;
    }
    #cursor-mask #cursor {
        will-change: transform, width, height;
        width: 0;
        height: 0;
        border-radius: 100%;
        position: absolute;
        top: 0;
        left: 0;
        pointer-events: none;
        background-color: rgba(255,255,255,0.2);
    }
    #cursor-mask #cursor img {
        width: 0;
        height: 0;
        position: relative;
        transform: translate(-50%,-50%);
        left: 50%;
        top: 50%;
        opacity: 0;
        transition: 0.3s;
    }

    #cursor.hover {
        opacity: 1;
    }

    #cursor.hover-large img{
        width: 4rem;
        height: 4rem;
        opacity: 0.9;
    }
</style>