<template>
    <div class="project-content">
        <h2>{{projectTitle}}</h2>
        <img :src="item" v-for="item of detailsPage" :key='item' alt=""/>
    </div>
</template>

<script>
    import ProjectList from "@/components/ProjectList/ProjectList";
    export default {
        name: "ProjectDetailsPage",
        data(){
            return{
                pTitle: this.projectTitle,
                dPage: this.detailsPage,
            }
        },
        computed:{
            id: function (){
                return this.$route.params.id
            },
            projectTitle: function (){
                return ProjectList.data().projectData[this.id - 1].title
            },
            detailsPage : function () {
                return ProjectList.data().projectData[this.id -1].detailsPage
            }
        }

    }
</script>

<style scoped>
    .project-content{
        min-height: 100vh;
    }
    .project-content h2{
        margin-bottom: 2rem;
    }
    .project-content img{
        width: 100%;
        height: auto;
        object-fit: cover;
    }
</style>