<template>
    <div id="webgl"></div>
</template>

<script type = "module">
    import * as THREE from 'three';
    // import * as dat from 'dat.gui';


    export default {
        name: "WebGL",
        data() {
            return {
                camera: null,
                scene: null,
                renderer: null,
                mesh: null,
                mesh2: null,
                mesh3: null,
                controls:null,
                sizes:{
                    width:0,
                    height:0
                },
                canvas:null,
                geometry1:null,
                geometry2:null,
                geometry3:null,
            };
        },
        mounted(){
            this.init();
            this.animate();
            this.switchWEBGL(this.status);
        },
        methods:{
            init: function () {
                // const gui = new dat.GUI();

                //Canvas
                this.canvas = document.getElementById('webgl');

                //Scene
                this.scene = new THREE.Scene({
                });

                //Geometry
                this.geometry1 = new THREE.CylinderGeometry(0.25,0.25,0.6,64);
                this.geometry2 = new THREE.BoxGeometry(0.5,0.5,0.5);
                this.geometry3 = new THREE.TorusGeometry(0.25,0.1,64,64);


                //Material
                const material = new THREE.MeshPhysicalMaterial();
                material.color = new THREE.Color(0x202022);
                material.metalness = 0.4;
                material.roughness = 0.6;
                material.reflectivity = 0.8;
                material.flatShading = true;
                // material.wireframe = true;


                //Mesh
                this.mesh = new THREE.Mesh(this.geometry1,material);
                this.scene.add(this.mesh);
                this.mesh.position.x = 0;
                this.mesh.position.z = 0;
                this.mesh.rotation.x =0.6;
                this.mesh.rotation.z =0.6;

                // this.mesh2 = new THREE.Mesh(geometry2,material);
                // this.scene.add(this.mesh2);
                // this.mesh2.position.x = -1;
                // this.mesh2.position.z = -1;
                // this.mesh2.rotation.x =0.6;
                // this.mesh2.rotation.z =0.6;


                // gui.add(this.mesh2.position,'x').max(1).min(-1);
                // gui.add(this.mesh2.position,'y').max(1).min(-1);
                // gui.add(this.mesh2.position,'z').max(1).min(-1);


                // this.mesh3 = new THREE.Mesh(geometry3,material);
                // this.scene.add(this.mesh3);
                // this.mesh3.position.x = 1;
                // this.mesh3.position.z = -1;
                // this.mesh3.rotation.x =0.6;
                // this.mesh3.rotation.z =0.6;


                //Light
                const pointLight = new THREE.PointLight(0xccccff,1);
                pointLight.position.x = -3;
                pointLight.position.y = 5;
                pointLight.position.z = 0;
                pointLight.intensity = 1;
                this.scene.add(pointLight);

                const pointLight2 = new THREE.PointLight(0xffffff,1);
                pointLight2.position.x = 3;
                pointLight2.position.y = 5;
                pointLight2.position.z = 0;
                pointLight2.intensity = 1;
                this.scene.add(pointLight2);


                //Size
                this.sizes = {
                    width : this.canvas.clientWidth,
                    height : this.canvas.clientHeight
                };
                window.addEventListener('resize',()=>{
                    this.sizes.width = this.canvas.clientWidth;
                    this.sizes.height = this.canvas.clientHeight;
                    this.resize();
                });


                //Camera
                this.camera = new THREE.PerspectiveCamera(75,this.sizes.width / this.sizes.Height, 0.1,100);
                this.camera.position.x = 0;
                this.camera.position.y = 0;
                this.camera.position.z = 1;
                this.scene.add(this.camera);

                //Renderer
                this.renderer = new THREE.WebGLRenderer({
                    alpha: true,
                });

                this.canvas.appendChild(this.renderer.domElement);
                this.resize();
            },
            resize(){
                this.camera.aspect = this.sizes.width / this.sizes.height;
                this.camera.updateProjectionMatrix();
                this.renderer.setSize(this.sizes.width,this.sizes.height);
                this.renderer.setPixelRatio(Math.min(window.devicePixelRatio,2));
            },
            animate: function () {
                requestAnimationFrame(this.animate);
                this.mesh.rotation.x +=0.001;
                // this.mesh.rotation.y +=0.002;
                this.mesh.rotation.z +=0.001;
                this.renderer.render(this.scene,this.camera);
            },
            switchWEBGL(s){
                switch(s){
                    case 'home':
                        this.sizes.height =  this.canvas.clientHeight;
                        this.resize();
                        this.mesh.geometry = this.geometry1;
                        break;
                    case 'works':
                        this.sizes.height = this.canvas.clientHeight;
                        this.resize();
                        this.mesh.geometry = this.geometry2;
                        break;
                    case 'contact':
                        this.sizes.height = this.canvas.clientHeight;
                        this.resize();
                        this.mesh.geometry = this.geometry3;
                        break;
                    case 'project':
                        this.sizes.height = this.canvas.clientHeight;
                        this.resize();
                        break;
                }
            }
        },
        watch:{
            status:function () {
                this.switchWEBGL(this.status);
                console.log(this.$route.params.status)
            }
        },
        computed:{
            status:function () {
                return this.$route.params.status;
            }
        }
    }
</script>

<style scoped>
    #webgl{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
    }
</style>