import {gsap} from "gsap";import {Power2} from "gsap";
<template>
    <ProjectList :showcase="showcase" id="showcase"></ProjectList>
</template>

<script>
    import ProjectList from "@/components/ProjectList/ProjectList";
    import $ from "jquery";
    import {gsap,Power2} from 'gsap';
    export default {
        name: "Showcase",
        data(){
            return{
                showcase:[]
            }
        },
        mounted(){
            this.pickContent(this.status);
        },
        watch:{
            status:function () {
                gsap.to($('#showcase'),{
                    duration:0.5,
                    y:-32,
                    opacity:0,
                    ease:Power2.easeOut,
                });
                setTimeout( ()=>{
                    this.pickContent(this.status)
                },500);
                gsap.to($('#showcase'),{
                    delay:0.5,
                    duration:0.5,
                    y:32,
                    opacity:1,
                    ease:Power2.easeOut,
                });
            }
        },
        computed:{
            status:function () {
                return this.$route.params.status;
            }
        },
        methods:{
            pickContent(s) {
                switch (s){
                    case 'home':
                        this.showcase = ProjectList.data().projectData.reverse().filter(function (item) {
                            return item.isShowcases;
                        });
                        break;
                    case 'works':
                        this.showcase = ProjectList.data().projectData.reverse();
                        break;
                    case 'contact':
                        this.showcase = []
                        break;
                    default:
                        this.showcase = []
                        break;
                }
            }
        },
        components:{
            ProjectList
        },
    }
</script>

